.container {
    padding: 20px;
    font-family: Arial, sans-serif;
}

.header {
    color: #1A73E8;
}

.searchContainer {
    margin-bottom: 20px;
}

.searchInput {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ddd;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    text-align: center;
}

.imagePlaceholder {
    width: 100%;
    height: 150px;
    background-color: #f0f0f0;
    border-radius: 8px;
    margin-bottom: 10px;
}

.title {
    font-size: 18px;
    margin: 10px 0;
}

.price {
    font-size: 16px;
    font-weight: bold;
}
